@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Roboto:wght@300;400;900&display=swap);
@import url(https://fonts.googleapis.com/css2?family=Lato:wght@400;900&family=Roboto:wght@300;400;900&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.App {
  text-align: center;
  background-color: #282c34;
  min-height: 160vh;
  font-family: 'Roboto', sans-serif;
  display: flex;
  flex-direction: column;
  align-items: center;
  cursor: default;
}

.App-header {
  min-height: 110vh;
  display: flex;
  /* flex-direction: column; */
  align-items: center;
  justify-content: center;
  font-size: calc(80px + 2vmin);
  color: white;
  font-weight: 900;
  -webkit-animation: Move-header 2s ease-in-out 1s forwards;
          animation: Move-header 2s ease-in-out 1s forwards;
}

.App-header-name{
  text-decoration: none;
}

.App-header-subtext{
  margin-top: -2px;
  font-weight: 300;
  font-size: calc(5px + 2vmin);
  color: rgba(255,255,255, 0);
  -webkit-animation : Header-subtext-fadein 2s linear 3s forwards;
          animation : Header-subtext-fadein 2s linear 3s forwards;
}

.App-header-right{
  max-width : 70vh;
  max-height: 60px;
  display: flex;
  align-items: center;
  font-weight: 300;
  font-size: calc(5px + 2vmin);
  text-align: left;
  color: rgba(255,255,255, 0);
  /* border-left: solid lightgrey; */
  padding-left: 3vh;
  -webkit-animation : Header-subtext-fadein 2s ease-out 4s forwards;
          animation : Header-subtext-fadein 2s ease-out 4s forwards;
  postion: absolute;
}

.App-header-left{
  min-width: 70vh;
  position: in-block;
}


.App-link {
  color: #61dafb;
}

.Contact{
  position: fixed;
  top: 3%;
  right : -1%;
  background-color: PaleVioletRed;
  border-radius: 4px;
  font-size: 13px;
  padding: 10px 30px 10px 14px;
  color: white;
  border : none;
  font-weight: 900;
  cursor:pointer;
  transition: background-color 1s;
}

.Contact:hover{
	-webkit-animation: gradient 5s ease infinite;
	        animation: gradient 5s ease infinite;
}

@-webkit-keyframes gradient {
  0%{
    background-color: PaleVioletRed;
  }
  25% {
		background:MediumOrchid;
	}
	50% {
    background: MediumPurple;
	}
	75% {
    background: thistle;
	}
  100%{
    background-color: PaleVioletRed;
  }
}

@keyframes gradient {
  0%{
    background-color: PaleVioletRed;
  }
  25% {
		background:MediumOrchid;
	}
	50% {
    background: MediumPurple;
	}
	75% {
    background: thistle;
	}
  100%{
    background-color: PaleVioletRed;
  }
}

@-webkit-keyframes Header-subtext-fadein{
  from {
    color: rgba(255,255,255, 0);
  }
  to {
    color: rgba(255,255,255, 1);
  }
}

@keyframes Header-subtext-fadein{
  from {
    color: rgba(255,255,255, 0);
  }
  to {
    color: rgba(255,255,255, 1);
  }
}

@-webkit-keyframes Move-header{
  from {
    font-size: calc(80px + 2vmin);
    min-height: 110vh;
  }
  to{
    font-size: calc(30px + 2vmin);
    min-height: 20vh;

  }
}

@keyframes Move-header{
  from {
    font-size: calc(80px + 2vmin);
    min-height: 110vh;
  }
  to{
    font-size: calc(30px + 2vmin);
    min-height: 20vh;

  }
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

.content-wrapper{
  display: flex;
  flex-direction: column;
  align-items: flex-end;
  justify-content: space-around;
  min-width: 600px;
  padding-right: 80px;
  -webkit-animation : content-transition .5s ease-in 0s forwards;
          animation : content-transition .5s ease-in 0s forwards;
  opacity: 0;
/*  transition : 2s linear 4s forwards;*/
}

@-webkit-keyframes content-transition{
  0%{
    opacity:0;
  }
  100%{
    opacity: 1;
  }
}

@keyframes content-transition{
  0%{
    opacity:0;
  }
  100%{
    opacity: 1;
  }
}

